@import "_config.scss";

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: $font-stack;
  overflow: hidden;
}

.Wrapper {
  margin: 0 10%;
  height: 100vh;

  .scroll-down {
    text-transform: uppercase;
  }

  .tagline {
    position: absolute;
    top: 50%;
    right: -32px;
    transform: rotateZ(270deg);
    text-transform: uppercase;
  }
}

@import "_menu.scss";
@import "_content.scss";
